section.contact {
	margin-bottom: 144px;
}
.contact__title {
	margin-bottom: 50px;
	text-align: center;
}
.contact__img {
	display: flex;
	align-items: center;
	justify-content: center;
}
.contact__img a+a {
	margin-left: 20px;
}
.contact-form__title{
	line-height: 1.2;
	font-size: 23.8px;
	margin-bottom: 8px;
	text-align: center;
}
.contact__img a {
	display: flex;
	border: 1px solid #ebf3f2;
	padding: 9px;
	align-items: center;
	justify-content: center;
}
.contact__img img,
.contact__img svg {
	width: 60px;
	height: auto;
}

.contact {
	.wpcf7 {
		&:not(:last-child) {
			margin-bottom: 4rem;
		}
	} 

	&__text {
		text-align: center;
		font-weight: 300;
		margin-bottom: 12px;
		
		@media(min-width:  993px) {
			margin-top: 34px;
			margin-bottom: 45px;
		}
	}

	&__container {
		display: grid;
		grid-template-columns: repeat(2, 1fr);
		gap: 4rem;

		&+& {
			margin-top: 20px;
		}
		
		@media(max-width:  992px) {
			grid-template-columns: 1fr;
		}
	}

	&__content {
		margin-top: 30px;
		margin-bottom: 20px;

		a {
			transition: all .2s ease-in-out;
			color: #15e6cd;

			&:hover {
				color: var(--red);
				text-decoration: underline;
			}
		}
	}
	
	&__collage {
		display: grid;
		grid-template-columns: repeat(2, 1fr);
		gap: 10px;
	}

	.media-wrapper {
		font-size: 0;
		
		img {
			aspect-ratio: 1.323;
			width: 100%;
			object-fit: cover;
			object-position: center;
		}
	}
	
	iframe {
		aspect-ratio: 1.333333;
		border: 0;
		width: 100%;
		height: auto;
	}

	.form-group:last-of-type {
		margin-bottom: 0;
	}
}
section.contact textarea {
	height: 136px;
}
section.contact .btn-cyan {
	cursor: pointer;
	display: flex;
	align-items: center;
	justify-content: center;
	margin:  20px auto 0;
	max-width: 174px;
	width: 100%;
	font-size: 18px;
	padding: 5px 7px;
	font-weight: 700;
	line-height: 1.5;
}

@media(max-width:  1199px) {
	section.contact {
		margin-bottom: 60px;
	}
}
@media(max-width:  992px) {
	.telegram-subscribe,
	.contact__img a  {
		animation: 3s wiggle ease infinite;
	}
}
@media(max-width:  600px) {
	section.contact .btn-cyan {
		margin: 10px auto 0;
	}
}

@keyframes wiggle {
	0% {
		transform: rotate(-3deg);
		box-shadow: 0 2px 2px rgba(0, 0, 0, .2);
	}
	10% {
		transform: rotate(10deg);
	}
	20% {
		transform: rotate(-5deg);
	}
	30% {
		transform: rotate(1deg);
	}
	40% {
		transform: rotate(-1deg);
	}
	50% {
		transform: rotate(0);
		box-shadow: 0 2px 2px rgba(0, 0, 0, .2);
	}
	100% {
		transform: rotate(0);
		box-shadow: 0 2px 2px rgba(0, 0, 0, .2);
	}
}
